import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    theFirstLink: '',
    luckyDrawCount: 0
  },
  mutations: {
    setTheFirstLink(state, url) {
      state.theFirstLink = url
    },
    setLuckyDrawCount(state, count) {
      state.luckyDrawCount = count
    }
  },
  actions: {}
})
